@media (max-width: 480px) {
  .hardCodedText2 {
    margin-left: 5vw;
    font-size: 2vh;
  }

  .logoImg {
    content: url(/ywc-website/static/media/klklogomobile.8c3bf438.png);
    width: 90px;
    height: 55px;
  }

  .searchBar {
    width: 95%;
    height: 1vh;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 30px 10px 10px 10px;
    margin-bottom: 10px;
  }

  .filterButton {
    margin-left: 10px;
    background-color: Transparent;
    border: none;
  }
}

.search {
  width: 80%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 1.8px solid rgb(207, 206, 206);
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 8px 0 0 8px;
  outline: none;
  color: #dfdfdf;
  font-family: "Kanit", sans-serif;
}

.searchTerm:focus {
  color: rgb(71, 71, 71);
}

.searchButton {
  width: 70px;
  height: 34px;
  border: 1.8px solid rgb(207, 206, 206);
  border-left: none;
  background: rgb(236, 235, 235);
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body {
  font-family: "Kanit", sans-serif;
}

.grid {
  display: grid;
  min-width: 480px;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "leftBody rightBody";
  grid-column-gap: 4vw;
  -webkit-column-gap: 4vw;
          column-gap: 4vw;
  padding: 1vw;
}

.leftBody {
  grid-area: leftBody;
  border: 1px solid rgb(200, 200, 200);
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: auto;
  padding: 1vw;
}
.textHeadLeft {
  font-size: 2vmin;
  font-weight: 1000;
  padding: 10px;
}

.textHeadLeft2 {
  font-size: 2vmin;
  font-weight: 1000;
  padding: 7px;
  margin-top: 20px;
}
.rightBody {
  grid-area: rightBody;
  border: none;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.ordinaryText {
  font-size: 1.7vmin;
  color: rgb(78, 78, 78);
}

input[type="radio"] {
  transform: scale(0.75);
}

.moreoverdiv {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.moreoverbutton {
  text-align: center;
  width: 350px;
  height: 40px;
  color: rgb(78, 78, 78);
  background: white;
  border: 1px solid rgb(184, 183, 183);
  border-width: 0.1px;
  border-radius: 2px;
  font-family: "Kanit", sans-serif;
}

@media (max-width: 480px) {
